import React from 'react'
import Radium from 'radium'
import { color, fontSize, padding } from './CommonStyles'
import Header from './Header'
import Divider from './Divider'
import { ReactComponent as Momimsafe } from '../Assets/momimsafe.svg'
import { ReactComponent as Live } from '../Assets/live.svg'
import WebFrame from './WebFrame'
import Antidote from './Antidote'
import Labour from './Labour'
import Thanks from './Thanks'

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    backgroundColor: color.pureTeal,
    borderRadius: fontSize.extraSmall,

    '@media (min-width: 768px)': {  
      margin: padding.big
    },

    '@media (min-width: 1024px)': {  
      // no change.
      margin: padding.huge
    },
    '@media (min-width: 1200px)': {  
      // no change.
      margin: '8%',
      borderStyle: 'dotted',
      borderColor: color.sunLight,
      borderWidth: '3px'
    }
  },

  logoWrapper: {
    display: 'flex',
    alignSelf: 'center',
    justifyContent: 'center',
    marginTop: padding.big,
    
    '@media (min-width: 768px)': {  
      marginTop: padding.veryBig,
    },

    '@media (min-width: 1024px)': {  
        // no change.
        marginTop: padding.extraBig
        
    },
    '@media (min-width: 1200px)': {  
      // no change.
      marginTop: padding.huge,
      width: '120%',
      height: '120%'
    }
  },

  logoStyle: {
    width: '70%',
    height: '70%',
  },

  seconds: {
    position: 'fixed',
    top: 0
  }
};

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state={
      timer: 0
    };

    this.appRef = React.createRef(); 
    this.containerHeight = 0; 
    this.currentHeight = 0; 
    this.incrementPixels = 0; 
    this.scrollTime=20; // After how many seconds should be scroll. 
    this.totalTime = 195 * 1000; // Milliseconds. (3 minutes)

    this.curTime = 0; 
  }

  render() {
    return (
      <div ref={this.appRef} style={styles.container}>
          {/* <div style={styles.seconds}>{this.state.timer}</div> */}
          <Header onPress={this.onStartScroll.bind(this)} />
          <Divider />
          <div style={styles.logoWrapper}>
            <Momimsafe style={styles.logoStyle} />
          </div>
          <Divider />
          <WebFrame isMom={true} title={"Momimsafe"} url="https://momimsafe.live" />
          <Divider />
          <div style={styles.logoWrapper}>
            <Live style={styles.logoStyle} />
          </div>
          <Divider />
          <WebFrame title={"Printer"} url="https://momimsafe.live/#/printer" />
          <Divider />
          <Antidote />
          <Divider />
          <Labour />
          <Divider />
          <Thanks />
      </div>
    );
  }

  calcHeight = () => {
    this.containerHeight = this.appRef.current.clientHeight;
  }

  onStartScroll = () => {
    this.setState({
      timer: 0
    });
    this.calcHeight(); 
    this.incrementPixels = this.scrollTime * (this.containerHeight / this.totalTime); 
    this.currentHeight = 0; 
    this.interval = setInterval(this.move.bind(this), this.scrollTime); 
    // 3 x 60 = 1800 seconds to give this out. 
  }

  move = () => {
    this.currentHeight += this.incrementPixels;
    // console.log(this.currentHeight);
    window.scrollTo(0, this.currentHeight); 
    console.log('Scroll');
    
    this.curTime += this.scrollTime; 
    if (this.curTime === 1000) {
      this.setState({
        timer: this.state.timer + 1
      }); 

      this.curTime = 0; 
    }

    if (this.currentHeight > this.containerHeight) {
      window.clearInterval(this.interval);
    }
  }
}

export default Radium(App);

// 1800 - height 
// 1 - height / 1800 (pixels to cover every second)
// 5 - 5 * height/ 1800 (300)
